import React from 'react'
import millify from 'millify'
import { Typography, Row, Col, Statistic } from 'antd'
import { Link } from 'react-router-dom'

import { useGetCryptosQuery } from '../services/cryptoApi'
import { Cryptocurrencies, News } from '../components'

const {Title} = Typography 

const HomePage = () => {
  const { data, isFetching } = useGetCryptosQuery(10)
  
  if(isFetching) return 'Loading...'

  const globalStats = data?.data?.stats; 
    
  return (
    <>
      <Title level={2.2} className='heading'>Global Crypto Stats</Title>
      <Row>
        <Col span={12}><Statistic title='Total Cryptocurrencies' value={globalStats?.total} /></Col>
        <Col span={12}><Statistic title='Total Exchanges' value={millify(globalStats?.totalExchanges)} /></Col>
        <Col span={12}><Statistic title='Total Market Cap' value={millify(globalStats?.totalMarketCap)} /></Col>
        <Col span={12}><Statistic title='Total 24h Volume' value={millify(globalStats?.total24hVolume)} /></Col>
        <Col span={12}><Statistic title='Total Markets' value={millify(globalStats?.totalMarkets)} /></Col>
      </Row>
      <div className='home-heading-container'> 
        <Title style={{fontSize:"1.8rem"}} level={2} className='home-title'>Top 10 Cryptocurrencies in the world</Title>
        <Title style={{fontSize:"1.2rem"}}  level={3} className='show-more'><Link to='/cryptocurrencies'>Show More</Link></Title>
      </div>
      <Cryptocurrencies simplified />
      <div className='home-heading-container'>
        <Title style={{fontSize:"1.8rem"}} level={2} className='home-title'>Latest Crypto News</Title>
        <Title style={{fontSize:"1.2rem"}} level={3} className='show-more'><Link to='/news'>Show More</Link></Title>
      </div>
      <News simplified />

    </>
  )
}

export default HomePage
